.country-gift {
    &__breadcrumbs {
        margin: 16px 0; }
    &__main-title {
        text-align: center;
        margin-bottom: 16px; }
    &__filter {
        justify-content: center;
        margin-bottom: 16px;
        .select-with-tooltip__select {
            width: 270px;
            @include sm {
                width: 200px; } } } }

.gift-basket-to {
    &__container {
        @include clr; }
    &__main {
        // margin-left: 316px
        float: left;
        width: calc(100% - 292px);
        padding-left: 27px; }
    &__descr {
        @include clr; }
    &__subtitle {
        font-size: 20px;
        margin-bottom: 22px; }
    &__delivery {
        float: left;
        width: 530px;
        padding-right: 30px; }
    &__service {
        margin-left: 566px;
        @include clr; }
    .delivery-card {
        margin-bottom: 7px; }
    &__title {
        font-family: $corp;
        font-size: 30px;
        font-weight: 700;
        color: $text_black;
        letter-spacing: -0.7px;
        margin-bottom: 14px; }
    &__main-title {
 }        // margin-top: 50px
    &__cards {
        margin-bottom: 42px; }
    &__motto-bottom {
        text-align: center; }
    &__video {
        text-align: center;
        margin-bottom: 32px;
        margin-top: 22px; } }






