.cards {
	border-left: 1px solid $card-border-color;
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: stretch;
	// +sm
 }	// 	justify-content: center

.card {
	border-right: 1px solid $card-border-color;
	border-bottom: 1px solid $card-border-color;
	box-shadow: inset 0 1px 0 0 $card-border-color;
	padding: 22px 0;
	text-align: center;
	font-family: $corp;
	margin-bottom: -1px;
	&.col-1 {
		flex: 0 0 100%; }
	&.col-2 {
		flex: 0 0 50%; }
	&.col-3 {
		flex: 0 0 33.33%;
		@include sm {
			flex: 0 0 50%; }
		@include xs {
			flex: 0 0 100%; } }
	&.col-4 {
		flex: 0 0 25%;
		@include md {
			flex: 0 0 33.33%; }
		@include sm {
			flex: 0 0 50%; }
		@include xs {
			flex: 0 0 100%; } }
	&.col-5 {
		flex: 0 0 20%;
		@include sm {
			flex: 0 0 50%; }
		@include xs {
			flex: 0 0 100%; } }
	@include hover {
		.card__title {
			color: $main-green;
			@include colorTrans; } }
	&__big {
		padding: 22px 18px;
		.card__title {
			min-height: 44px; }
		.card__image {
			padding-bottom: 275px; } }
	&__middle {
		padding: 22px 10px; }
	&__image {
		display: block;
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		position: relative;
		margin-bottom: 16px; }
	&__freemark {
		position: absolute;
		bottom: 0;
		right: 0;
		.icon {
			font-size: 3em;
			svg {
				height: 30px;
				width: 30px; } } }
	&__title {
		display: block;
		font-size: 22px;
		font-weight: bold;
		font-style: normal;
		font-stretch: condensed;
		line-height: 1.09;
		text-align: center;
		color: $card-title-color;
		text-transform: uppercase;
		@include colorTrans;
		min-height: 56px;
		letter-spacing: -0.6px;
		h3 {
			font-size: inherit; } }
	&__price {
		margin-bottom: 0.5em;
		font-size: 21px;
		font-weight: bold;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: center;
		color: $card-price-color; }
	&__id {
		font-size: 21px;
		font-weight: bold;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: center;
		margin-bottom: 0.5em;
		&--id {
			color: $id-color-lighter; }
		&--number {
	  	color: $id-color-darker; } }
	&__divider {
		margin: 16px auto; }
	&__button-link {
		display: inline-block;
		text-align: center;
		color: $white;
		border-radius: 4px;
		text-align: center;
		text-transform: uppercase;
		padding: 7px 40px;
		border-radius: 20px;
		background-color: $main-green;
		@include colorTrans;
		@include hover {
			background-color: $dark-green;
			@include colorTrans; } } }

// .card.col-3
// 	+sm
// 		flex: 0 0 50%
// 	+xs
// 		flex: 0 0 100%

.small-card {
	font-family: $corp;
	display: block;
	min-width: 97px;
	font-family: $corp;
	text-align: center;
	&__image {
		@include cover;
		padding-bottom: 80px; }
	&__title {
		margin-top: 14px;
		font-size: 18px;
		font-weight: bold;
		line-height: 1.22;
		letter-spacing: -0.5px;
		color: $dark_blue; }
	&__id {
		font-size: 13px;
		line-height: normal;
		color: $text_black; }
	&__price {
		font-size: 15px;
		font-weight: 900;
		line-height: normal;
		color: $blue; } }



.card__slider-small {
	.card__image {
		padding-bottom: 200px; }
	.card__title {
		font-size: 20px;
		letter-spacing: -1px;
		padding: 18px; } }

.card__middle {
	.card__image {
		padding-bottom: 300px; }
	.card__title {
		min-height: 68px;
		padding: 0 12px; } }


// ===== ICON-CARDS =====

.card-with-icons {
	border-radius: 5px;
	&-title {
		font-size: 22px;
		font-weight: bold;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.23;
		letter-spacing: -0.6px;
		text-align: left;
		color: $blue; }
	&-text {
		font-family: CorpidE1sSCd;
		font-size: 20px;
		font-weight: 300;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.35;
		letter-spacing: -0.5px;
		text-align: left;
		color: $dark_blue; } }

.time-card {
	background-color: $special_bg;
	height: 100%;
	padding: 12px 14px 12px 10px;
	&__image {
		float: left;
		margin-right: 7px;
		font-size: 0;
		.icon {
			font-size: 40px; } }
	&__title {
		margin-bottom: 14px;
		margin-top: 14px; } }


.delivery-card {
	display: flex;
	align-items: center;
	background-color: rgba($main-green, 0.3);
	border-radius: 5px;
	padding: 10px 14px 14px 20px;
	&--blue {
		background-color: $special_bg;
		.delivery-card__img .icon {
			font-size: 60px; } }
	&--dark-green {
		background-color: $main-green; }
	&__img {
		font-size: 0;
		flex: 0 0 80px;
		.icon {
			fill: $blue;
			font-size: 70px;
			display: inline-block;
			vertical-align: middle; } }
	&__text {
		font-size: 24px; } }

.card-sm-buttons {
	display: none;
	position: relative;
	height: 60px;
	& > * {
		display: inline-block; }
	@include sm {
		display: block; } }

.sm-filter-btn {
	@include vcenter; }
