 }    //     font-weight: bold
.subtitle {
    font-family: $corp;
    font-size: 20px;
    font-weight: 700;
    color: $text_black; }

.text-big {
    font-size: 20px;
    font-family: $corp;
    line-height: 1.22;
    p {
        margin-bottom: 22px; }
    &--italic {
        font-style: italic; } }
