.divider-grey {
	width: 100%;
	height: 2px;
	background-repeat: repeat-x;
	background-size: contain;
	background-size: 6px 2px;
	background-image: url(../assets/img/general/divider-grey.svg); }

.divider-green {
	@extend .divider-grey;
	background-image: url(../assets/img/general/divider-green.svg); }


.divider-blue {
	@extend .divider-grey;
	background-image: url(../assets/img/general/divider-blue.svg); }
