.filter {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-family: $corp;
    @include sm {
        visibility: hidden;
        opacity: 0;
        transition: opacity 1.5s, visibility 1.5s;
        &.is-open {
            display: block;
            visibility: visible;
            opacity: 1;
            transition: opacity 1.5s, visibility 1.5s; } }
    &--center {
        justify-content: center;
        flex-wrap: wrap;
        .filter__select-wrapper {
            flex: 0 0 27%;
            @include sm {
                width: 100%; } } }

    &--single {
        justify-content: center; }
    @include sm {
        flex-flow: column; }
    &__filter {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        margin-left: auto;
        @include sm {
            flex-flow: column;
            width: 100%;
            align-items: flex-start;
            justify-content: center;
            padding: 0 30px; } }
    &__text {
        min-width: 440px;
        @include sm {
            min-width: inherit; } }


    &__found {
        font-family: $corp;
        font-size: 24px;
        font-weight: 300;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: left;
        color: #3a5864;
        margin-right: auto;
        &--bold {
            font-weight: 700; } }
    &__select-wrapper {
        padding: 0 20px;
        font-size: 0;
        text-align: center;
        @include sm {
            padding: 0 0 20px 0;
            text-align: center;
            flex: 0 0 100%; }
        // margin-bottom: 26px
        & > * {
            display: inline-block;
            vertical-align: middle; } }
    &__country-flag {
        max-height: 30px;
        max-width: 40px;
        overflow: hidden;
        img {
            max-height: 30px;
            display: inline-block;
            vertical-align: middle; } }
    &__select-image {
        margin: 0 6px;
        .icon {
            font-size: 28px; }
        @include sm {
            display: none; } }
    &__select {
        width: 232px; }
    &__select-label {
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
        color: $text_black;
        width: 100%;
        margin-bottom: 8px; }
    &__tooltip {
        margin-left: 6px;
        cursor: pointer;
        .icon {
            font-size: 28px; } }
    &__range {
        // display: inline-block
        flex: 0 0 400px;
        // width: 400px
        margin-top: 26px;
        @include sm {
            margin-top: 0; } }
    &__breadcrumb {
        margin-right: auto; }

    &__btn {
        flex: 0 0 100%;
        text-align: center;
        width: 100%;
        margin-top: 20px; }
    &__or {
        display: inline-block;
        height: 100%;
        vertical-align: bottom;
        align-self: flex-end;
        line-height: 30px;
        @include sm {
            align-self: center;
            margin-top: -20px; } } }
